<template>
  <div id="box">
    <div class="head">
      <div class="top">
        <ul class="zhongxian">
          <li>{{data.total || 0}}</li>
          <li>我的佣金</li>
        </ul>|
        <ul>
          <li>{{data.money || 0}}</li>
          <li>结算中{{data.level>2?"佣金":"奖金"}}</li>
        </ul>
      </div>
    </div>
    <div class="info">
      <div class="list">
        <span class="title bg2">团队收入数据</span>
        <div>
          <span>{{data.totaldistribut || 0}}</span>
          <span>总计</span>
        </div>
        <div>
          <span>{{data.moneydistribut || 0}}</span>
          <span>结算中</span>
        </div>
        <div>
          <span>{{data.orderenddistribut || 0}}</span>
          <span>完成订单</span>
        </div>
        <div>
          <span>{{Number(data.orderstartdistribut) || 0}}</span>
          <span>交易中订单</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
// import { NavBar } from "vant";
/* import { shuju } from "@/axios/getData"; */
export default {
  data() {
    return {
      data: {}
    };
  },
  created() {
    this.getdata();
  },
  methods: {
    async getdata() {
      var res = await shuju();
      this.data = res.data;
      console.log(res.data);
    }
  },
  components: {
    // vanNavBar:NavBar
  }
};
</script>
<style lang="scss" scoped>
li {
  list-style: none;
}
#box {
  .head {
    background: url(../../assets/images/data_bg@2x.png) no-repeat;
    background-size: 100% 100%;
    height: 102px;

    .top {
      display: flex;
      align-items: center;
      justify-content: space-around;
      width: 100%;
      color: #fff;
      ul {
        font-weight: 500;
        text-align: center;
        margin-top: 10px;
        & > li:nth-child(1) {
          font-size: 20px;
        }
      }
    }
  }
  .info {
    margin: -27px 60px;
    background: #fff;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    .bg2 {
      background: url(../../assets/images/data_hy@2x.png) no-repeat 16% center;
      background-size: 20px;
    }
    .title {
      padding: 20px 0;
      width: 100%;
      text-align: center;
      color: #454545;
      font-size: 16px;
    }
    .list {
      margin-top: 20px;
      display: flex;
      flex-wrap: wrap;
      flex-direction: row;
      width: 100%;
      div {
        display: flex;
        flex-direction: column;
        font-weight: 500;
        text-align: center;
        width: 50%;
        margin-bottom: 50px;
        span {
          display: inline-block;
          margin-top: 20px;
          font-size: 10pt;
        }
        & > span:nth-child(1) {
          color: #ee241e;
          font-size: 11pt;
        }
      }
    }
  }
}
.img1 {
  position: absolute;
  z-index: -5;
}
</style>



